/* eslint-disable @typescript-eslint/no-explicit-any */
import { Component, OnInit } from '@angular/core';
import {
  BOSCreateTransactionInput,
  BOSPaymentService,
  CreatePaymentInput,
  CreateTransactionInput,
  DetailPaymentInput,
  DetailTransactionSupplierInput,
  PaymentMethod,
  PaymentMethodCategory,
  PaymentMethodType,
  PaymentService,
  PaymentStatusTypes,
  PaymentType,
  ResponseDetailPayment,
  RetryPaymentInput,
  SCMTransaction,
  SCMTransactionService,
  TransactionService
} from '@youtap-web-lib';
import {
  PaymentData,
  PaymentDataType
} from '../../../shared/data/payment.data';
import * as dayjs from 'dayjs';
import { ActivatedRoute, Router } from '@angular/router';
import { AuthData } from '../../../shared/data/auth.data';
import { Location } from '@angular/common';
import { SubscriptionData } from '../../../shared/data/subscription.data';
import { CountCartService } from '../../../shared/service/count-cart.service';
import { GaService } from '../../../shared/service/ga.service';
import { ReferralService } from '../../../shared/service/referral.service';

@Component({
  selector: 'list-payment',
  templateUrl: './list-payment.component.html'
})
export class ListPaymentComponent implements OnInit {
  isLoading = true;
  listAvailablePayment!: PaymentMethodCategory[];
  paymentMethodSelected!: PaymentMethod;
  total = 0;
  transaction = {} as SCMTransaction;
  va = {
    modal: false
  };
  qris = {
    modal: false
  };
  eWallet = {
    modal: false
  };
  modalStatus = {
    loading: false,
    success: false,
    failed: false,
    expired: false,
    unpaid: false
  };
  loadingCheckStatus = false;
  detailPayment!: ResponseDetailPayment;
  id = '';
  invoice = '';
  type = '';
  paymentType = '';
  transactionInvoice = '';
  isPodAvailable = '';
  isExpired = false;
  isRetry = false;
  pod = {
    loadingModal: false,
    successModal: false
  };
  showTncPOD = false;

  constructor(
    private paymentService: PaymentService,
    private bosPaymentService: BOSPaymentService,
    public paymentData: PaymentData,
    private scmTransactionService: SCMTransactionService,
    private transactionService: TransactionService,
    private router: Router,
    private route: ActivatedRoute,
    private authData: AuthData,
    private location: Location,
    private subscriptionData: SubscriptionData,
    private countCartService: CountCartService,
    private gaService: GaService,
    private referralService: ReferralService
  ) {}

  ngOnInit(): void {
    this.getAvailablePayments();
    this.total = this.paymentData.payment.total || 0;
    this.id = this.route.snapshot?.queryParamMap.get('id') ?? '';
    this.invoice = this.route.snapshot?.queryParamMap.get('invoice') ?? '';
    this.type = this.route.snapshot?.queryParamMap.get('type') ?? '';
    this.paymentType =
      this.route.snapshot?.queryParamMap.get('paymentType') ?? '';
    this.isPodAvailable = this.route.snapshot?.queryParamMap.get('pod') ?? '';

    if (this.id) {
      this.getDetailTransaction(this.id);
    }
    if (this.invoice) {
      this.subsDetailPayment(this.invoice);
    }
  }

  getAvailablePayments() {
    this.isLoading = true;
    this.paymentService.listAvailablePayment().subscribe({
      next: ({ data }) => {
        this.isLoading = false;
        this.listAvailablePayment =
          data.listAvailablePayment.paymentMethodCategories.filter((val) =>
            this.total > 9999 ? val : val.name !== 'virtual_account'
          );
      },
      error: (err) => {
        err;
      }
    });
  }

  replacePaymentName(name: string) {
    switch (name) {
      case 'bca_va':
        return 'BCA Virtual Account';
      case 'mandiri_va':
        return 'Mandiri Virtual Account';
      case 'bri_va':
        return 'BRI Virtual Account';
      case 'bni_va':
        return 'BNI Virtual Account';
      case 'mpm':
        return 'Scan QRIS';

      default:
        return name;
    }
  }

  makePaymentExpired(expiredAt: string) {
    setInterval(() => {
      const now = dayjs();
      const expired = dayjs(expiredAt);
      const diff = expired.diff(now, 'seconds');
      if (diff <= 0) {
        this.modalStatus.success = false;
        this.modalStatus.expired = true;
      }
    }, 1000);
  }

  createTransaction(
    payment: PaymentMethod,
    paymentMethodType: PaymentMethodType,
    paymentMethodCategory: PaymentMethodCategory
  ) {
    this.addTracker(payment, paymentMethodCategory);
    this.showModal(paymentMethodType);
    this.paymentMethodSelected = payment;
    this.modalStatus.loading = true;
    this.modalStatus.success = false;
    switch (this.paymentData.payment.type) {
      case PaymentDataType.BTOK:
        if (this.id && this.paymentType === PaymentType.PAY_ON_DELIVERY) {
          if (this.isRetry) {
            this.retryPayment(payment);
          } else {
            this.createPayment(payment);
          }
        } else {
          this.createTransactionBTOK(payment);
        }
        break;
      case PaymentDataType.SUBSCRIPTION:
        this.createTransactionSubscription(payment);
        break;

      default:
        break;
    }
  }

  addTracker(
    payment: PaymentMethod,
    paymentMethodCategory: PaymentMethodCategory
  ) {
    switch (paymentMethodCategory.name) {
      case 'virtual_account':
        this.gaService.addEvent('Payment_select_virtual_account', 'Payment', '');
        break;
      case 'qr_code':
        this.gaService.addEvent('Payment_select_qr_code', 'Payment', '');
        break;
      default:
        break;
    }
    switch (payment.name) {
      case 'bca_va':
        this.gaService.addEvent('Payment_VA_BCA', 'Payment', '');
        break;
      case 'mandiri_va':
        this.gaService.addEvent('Payment_VA_Mandiri', 'Payment', '');
        break;
      case 'bri_va':
        this.gaService.addEvent('Payment_VA_BRI', 'Payment', '');
        break;
      case 'bni_va':
        this.gaService.addEvent('Payment_VA_BNI', 'Payment', '');
        break;
      default:
        break;
    }
  }

  showModal(paymentMethodType: PaymentMethodType) {
    switch (paymentMethodType) {
      case PaymentMethodType.VA:
        this.va.modal = true;
        break;
      case PaymentMethodType.QR:
        this.qris.modal = true;
        break;
      case PaymentMethodType.POD:
        this.pod.loadingModal = true;
        break;
      case PaymentMethodType.EWALLET:
        this.eWallet.modal = true;
        break;
      default:
        break;
    }
  }

  createPayment(payment: PaymentMethod) {
    this.transaction = {} as SCMTransaction;
    const data = {
      createPaymentInput: {
        paymentMethod: payment.name,
        transactionRefId: this.transactionInvoice,
        transactionSupplierInvoice: this.invoice
      }
    } as CreatePaymentInput;
    this.paymentService.createPayment(data).subscribe({
      next: ({ data }: any) => {
        this.modalStatus.loading = false;
        this.modalStatus.success = true;
        this.transaction.payment = data.createPayment;
        this.transaction.total = data.createPayment.transactionAmount;
        this.getDetailTransaction(this.id);
      },
      error: (err) => {
        const [error] = err.graphQLErrors;
        this.gaService.addEvent(error.extensions.code, 'payment', '');
        if (error.extensions.code === 'OUT_OF_STOCK') {
          this.location.back();
        }
        this.modalStatus.loading = false;
        this.modalStatus.failed = true;
      }
    });
  }

  retryPayment(payment: PaymentMethod) {
    this.transaction = {} as SCMTransaction;
    const data = {
      retryPaymentInput: {
        paymentMethod: payment.name,
        transactionRefId: this.transactionInvoice,
        transactionSupplierInvoice: this.invoice
      }
    } as RetryPaymentInput;
    this.paymentService.retryPayment(data).subscribe({
      next: ({ data }: any) => {
        this.transaction.payment = data.retryPayment;
        this.transaction.total = data.retryPayment.transactionAmount;
        setTimeout(() => {
          this.modalStatus.loading = false;
          this.modalStatus.success = true;
        }, 500);
        this.checkExpired();
      },
      error: (err) => {
        const [error] = err.graphQLErrors;
        this.gaService.addEvent(error.extensions.code, 'payment', '');
        if (error.extensions.code === 'OUT_OF_STOCK') {
          this.location.back();
        }
        this.modalStatus.loading = false;
        this.modalStatus.failed = true;
      }
    });
  }

  createTransactionBTOK(payment: PaymentMethod) {
    this.transaction = {} as SCMTransaction;
    const data = {
      createTransactionInput: {
        couponCode: this.paymentData.payment.voucher,
        paymentMethod: payment.name
      }
    } as CreateTransactionInput;
    this.scmTransactionService.createTransaction(data).subscribe({
      next: ({ data }: any) => {
        this.countCartService.getCountCart();
        this.modalStatus.loading = false;
        this.modalStatus.success = true;
        this.transaction = data.createTransaction;
        if (payment.name === PaymentMethodType.POD) {
          this.pod.successModal = true;
        }
        if (data) {
          this.makePaymentExpired(this.transaction.payment.expiredAt);
        }
      },
      error: (err) => {
        const [error] = err.graphQLErrors;
        this.gaService.addEvent(error.extensions.code, 'payment', '');
        if (error.extensions.code === 'OUT_OF_STOCK') {
          this.location.back();

          // this.router.navigate(["/cart/payment"]);
        }
        this.modalStatus.loading = false;
        this.modalStatus.failed = true;
      }
    });
  }

  createTransactionSubscription(payment: PaymentMethod) {
    this.transaction = {} as SCMTransaction;
    const plan = this.paymentData.paymentSubscriptionData.planOption.map(
      (val) => {
        const referral = val.referralCode;
        if (referral) {
          val.referralCode = referral;
        }
        return val;
      }
    );
    const data = {
      createTransactionInput: {
        planOption: plan,
        paymentMethod: payment.name,
        couponCode: this.paymentData.payment.voucher
      }
    } as BOSCreateTransactionInput;
    this.transactionService.createSubscriptionTransaction(data).subscribe({
      next: ({ data }: any) => {
        this.countCartService.getCountCart();
        this.subscriptionData.removePlanCart();
        this.modalStatus.loading = false;
        this.modalStatus.success = true;
        this.transaction = data.createSubscriptionTransaction;
        if (data) {
          this.makePaymentExpired(this.transaction.payment.expiredAt);
        }
        this.paymentData.paymentSubscriptionData.planOption.forEach((val) => {
          this.referralService.useReferral(val.id);
        });
      },
      error: () => {
        this.modalStatus.loading = false;
        this.modalStatus.failed = true;
      }
    });
  }

  checkStatus(transactionRefId: string) {
    this.loadingCheckStatus = true;
    const data: DetailPaymentInput = {
      detailPaymentInput: {
        transactionRefId: transactionRefId
          ? transactionRefId
          : this.transactionInvoice
      }
    };
    this.bosPaymentService.detailPayment(data).subscribe({
      next: ({ data }) => {
        this.loadingCheckStatus = false;
        if (
          data.detailPayment.paymentStatus ===
          PaymentStatusTypes.AWAITING_PAYMENT
        ) {
          this.modalStatus.unpaid = true;
          setTimeout(() => {
            this.modalStatus.unpaid = false;
          }, 5000);
        } else {
          if(data.detailPayment.paymentType === 'VA') {
            this.gaService.addEvent('Payment_successful_VA', 'Payment', '');
          }
          if(data.detailPayment.paymentType === 'QRIS') {
            this.gaService.addEvent('Payment_successful_QRIS', 'Payment', '');
          }

          this.router.navigate(['/payment/paid'], {
            queryParams: {
              total: this.transaction.total,
              type: this.type,
              invoice: this.invoice
            },
            replaceUrl: true
          });
        }
      },
      error: () => {
        this.modalStatus.loading = false;
        this.modalStatus.failed = true;
      }
    });
  }

  subsDetailPayment(invoice: string) {
    const data: DetailPaymentInput = {
      detailPaymentInput: {
        transactionRefId: invoice
      }
    };

    this.bosPaymentService.detailPayment(data).subscribe({
      next: ({ data }) => {
        this.transaction = {
          id: data.detailPayment.id,
          status: data.detailPayment.paymentStatus,
          invoice: data.detailPayment.transactionRefId,
          payment: data.detailPayment,
          updatedAt: data.detailPayment.expiredAt,
          createdAt: data.detailPayment.expiredAt,
          total: data.detailPayment.transactionAmount,
          totalBeforeVoucher: data.detailPayment.transactionAmount,
          userId: this.authData.location.userId
            ? this.authData.location.userId
            : '',
          paymentStatus: data.detailPayment.paymentStatus
        };
        this.modalStatus = {
          loading: false,
          success: true,
          failed: false,
          expired: false,
          unpaid: false
        };
        this.checkExpired();
        const paymentType = data.detailPayment.paymentType;
        if (paymentType === 'VA') {
          this.va.modal = true;
        } else if (paymentType === 'QRIS') {
          this.qris.modal = true;
        } else if (paymentType === 'E_WALLET') {
          this.eWallet.modal = true;
        }
      }
    });
  }

  getDetailTransaction(id: string) {
    const data: DetailTransactionSupplierInput = {
      id: id
    };
    this.scmTransactionService.detailTransactionSupplier(data).subscribe({
      next: ({ data }) => {
        this.invoice = data.detailTransactionSupplier.invoice;
        this.transactionInvoice =
          data.detailTransactionSupplier.transaction.invoice;
        this.transaction = {
          id: data.detailTransactionSupplier.id,
          status: data.detailTransactionSupplier.status,
          invoice: data.detailTransactionSupplier.transaction.invoice,
          payment: data.detailTransactionSupplier.payment,
          total: data.detailTransactionSupplier.transaction.total,
          totalBeforeVoucher: data.detailTransactionSupplier.transaction.total,
          updatedAt: data.detailTransactionSupplier.updatedAt,
          createdAt: data.detailTransactionSupplier.updatedAt,
          userId: this.authData.location.userId
            ? this.authData.location.userId
            : '',
          paymentStatus: data.detailTransactionSupplier.payment?.paymentStatus
        };
        this.total = data.detailTransactionSupplier.transaction.total;
        this.modalStatus = {
          loading: false,
          success: true,
          failed: false,
          expired: false,
          unpaid: false
        };
        this.checkExpired();
        if (data.detailTransactionSupplier.payment) {
          if (data.detailTransactionSupplier.payment.paymentType === 'VA') {
            this.va.modal = true;
          } else if (
            data.detailTransactionSupplier.payment.paymentType === 'QRIS'
          ) {
            this.qris.modal = true;
          } else if (
            data.detailTransactionSupplier.payment.paymentType === 'E_WALLET'
          ) {
            this.eWallet.modal = true;
          }
        }
      },
      error: (err) => {
        return err;
      }
    });
  }

  checkExpired() {
    if (this.transaction.payment) {
      const now = dayjs();
      const expired = dayjs(this.transaction.payment.expiredAt);
      const diff = now.diff(expired, 'seconds');
      if (diff > 0) {
        this.isExpired = true;
        this.modalStatus.success = false;
        this.modalStatus.expired = true;
      } else {
        this.isExpired = false;
      }
    }
  }

  handleClosed() {
    if (this.id) {
      this.location.back();
    } else if (this.paymentData.payment.type === 'SUBSCRIPTION') {
      // if (this.invoice) {
      //   this.location.back();
      // } else {
      //   this.location.back();
      //   this.location.back();
      // }
      this.location.replaceState('/');
      this.router.navigate(['/subscription/history']);
    } else {
      this.router.navigate(['/transaction']);
    }
  }

  toListTransaction() {
    this.router.navigate(['/transaction'], {});
  }

  handleShowTnc() {
    this.showTncPOD = true;
  }

  retryOrToTransaction() {
    if (this.paymentType === PaymentType.PAY_ON_DELIVERY) {
      this.isRetry = true;
    } else {
      if (this.type === 'SUBSCRIPTION') {
        this.router.navigate(['/subscription/history']);
      } else {
        this.toListTransaction();
      }
    }
  }

  backToList() {
    this.modalStatus = {
      expired: false,
      failed: false,
      loading: false,
      success: false,
      unpaid: false
    };
    this.va.modal = false;
    this.qris.modal = false;
    this.eWallet.modal = false;
  }
}
