import { Component, Input, OnInit } from "@angular/core";
import { SafeUrl } from "@angular/platform-browser";
import { SCMTransaction } from "@youtap-web-lib";
import * as dayjs from "dayjs";

@Component({
  selector: "payment-qr-modal",
  templateUrl: "./payment-qr-modal.component.html"
})
export class PaymentQrModalComponent implements OnInit {
  @Input() transaction!: SCMTransaction;
  configCountDown = {
    leftTime: 0
  };
  isLoading = true;
  qrCodeDownloadLink: SafeUrl = "";
  isExpired = false;

  ngOnInit(): void {
    if (this.transaction) {
      const now = dayjs();
      const expired = dayjs(this.transaction.payment.expiredAt);
      const diff = now.diff(expired, "seconds");
      if(diff < 0) {
        this.configCountDown.leftTime = Math.abs(now.diff(expired, "seconds"));
      }
      else {
        this.isExpired = true;
        this.configCountDown.leftTime = 0;
      }
    }
  }

  onChangeURL(url: SafeUrl) {
    this.qrCodeDownloadLink = url;
  }

  downloadQR() {
    window.location.href = this.qrCodeDownloadLink.toString();
  }
}
