import { Inject, Injectable, PLATFORM_ID } from '@angular/core';
import { isPlatformBrowser } from '@angular/common';
import { PlanOption } from '@youtap-web-lib';

@Injectable({
  providedIn: 'root'
})
export class SubscriptionData {
  isReady = false;

  constructor(@Inject(PLATFORM_ID) private platformId: object) {
    if (isPlatformBrowser(this.platformId)) {
      this.isReady = true;
    }
  }

  addPlanCart(data: PlanOption) {

    if (this.isReady) {
      const existing = JSON.parse(
        localStorage.getItem('data_plan_cart') as string
      );

      let plan: PlanOption[] = existing ? existing : [];
      plan = [data];
      localStorage.setItem('data_plan_cart', JSON.stringify(plan));
    }
  }

  removePlanCart() {
    if (this.isReady) {
      localStorage.removeItem('data_plan_cart');
    }
  }

  changePlanCart(data: PlanOption) {
    if (this.isReady) {
      const existing = JSON.parse(
        localStorage.getItem('data_plan_cart') as string
      ) as PlanOption[];
      const plan: PlanOption[] = existing ? existing : [];
      const index = existing.findIndex((val) => val.plan.id === data.plan.id);
      plan[index] = data;
      localStorage.setItem('data_plan_cart', JSON.stringify(plan));
    }
  }

  get getPlanCart(): PlanOption[] {
    if (this.isReady) {
      const plan: PlanOption[] = JSON.parse(
        localStorage.getItem('data_plan_cart') as string
      );

      return plan;
    } else {
      return [];
    }
  }
}
