import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import {Location} from "@angular/common";

@Component({
  selector: 'paid-payment',
  templateUrl: './paid-payment.component.html'
})
export class PaidPaymentComponent implements OnInit {
  total = 0;
  type = '';
  invoice = '';

  constructor(private route: ActivatedRoute, private router: Router, private location: Location) {}

  ngOnInit(): void {
    this.total = parseInt(this.route.snapshot?.queryParamMap.get('total') ?? '0');
    this.type = this.route.snapshot?.queryParamMap.get('type') ?? '';
    this.invoice = this.route.snapshot?.queryParamMap.get('invoice') ?? '';
  }

  toTransaction() {
    if (this.type === 'SUBSCRIPTION') {
      this.location.replaceState('/');
      this.router.navigate(['/subscription/history']);
    } else {
      this.router.navigate(['/transaction']);
    }
  }
}
