import { Component, Input } from '@angular/core';

@Component({
  selector: 'failed-payment',
  templateUrl: './failed-payment.component.html'
})
export class FailedPaymentComponent {

  @Input() type = '';

  refresh() {
    window.location.reload()
  }

  replacePaymentType() {
    let paymentType = 'Metode Pembayaran';
    if(this.type === 'QRIS') {
      paymentType = 'Kode QR';
    }
    if(this.type === 'VA') {
      paymentType = 'Nomor Virtual Account';
    }
    if(this.type === 'EWALLET') {
      paymentType = 'Nomor Virtual Account';
    }
    return paymentType;
  }

}
