import { Clipboard } from "@angular/cdk/clipboard";
import { Component, Input, OnInit } from "@angular/core";
import {
  PaymentMethod,
  SCMTransaction
} from "@youtap-web-lib";
import * as dayjs from "dayjs";

@Component({
  selector: "payment-va-modal",
  templateUrl: "./payment-va-modal.component.html"
})
export class PaymentVaModalComponent implements OnInit {
  showDetail = false;
  @Input() transaction!: SCMTransaction;
  @Input() paymentMethod!: PaymentMethod;
  configCountDown = {
    leftTime: 0
  };
  isLoading = true;
  isExpired = false;

  constructor(
    private clipboard: Clipboard
  ) {
  }

  ngOnInit(): void {
    if (this.transaction) {
      const now = dayjs();
      const expired = dayjs(this.transaction.payment.expiredAt);
      const diff = now.diff(expired, "seconds");
      if(diff < 0) {
        this.configCountDown.leftTime = Math.abs(now.diff(expired, "seconds"));
      }
      else {
        this.isExpired = true;
        this.configCountDown.leftTime = 0;
      }
    }
  }

  copyToClipboard(value: string) {
    this.clipboard.copy(value);
    alert("Berhasil disalin!");
  }
}
