import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { BasicContainerComponent } from '../../shared/layout/container/basic-container/basic-container.component';
import { ListPaymentComponent } from './list-payment/list-payment.component';
import { PaidPaymentComponent } from './paid-payment/paid-payment.component';

const routes: Routes = [
  {
    path: '',
    component: BasicContainerComponent,
    children: [
      {
        path: 'list',
        component: ListPaymentComponent,
        title: 'Bayar Dengan'
      }
    ]
  },
  {
    path: 'paid',
    component: PaidPaymentComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PaymentRoutingModule { }
