/* eslint-disable @typescript-eslint/no-explicit-any */
import {Component, Input, OnDestroy, OnInit} from '@angular/core';
import {ActivatedRoute, Router} from '@angular/router';
import {
  BOSPaymentService,
  DetailPaymentInput,
  PaymentStatusTypes,
  SCMTransaction
} from '@youtap-web-lib';
import * as dayjs from 'dayjs';

@Component({
  selector: 'payment-ewallet-modal',
  templateUrl: './payment-ewallet-modal.component.html'
})
export class PaymentEwalletModalComponent implements OnInit, OnDestroy {
  @Input() transaction!: SCMTransaction;
  showDetail = false;
  configCountDown = {
    leftTime: 0
  };
  isExpired = false;
  modalDetail = false;
  type = '';
  intervalId!: any;
  loadingCheckStatus = false;
  isCheckStatusManual = false;

  constructor(
    private bosPaymentService: BOSPaymentService,
    private router: Router,
    private route: ActivatedRoute
  ) {
  }

  ngOnInit(): void {
    this.type = this.route.snapshot?.queryParamMap.get('type') ?? '';
    const now = dayjs();
    if (this.transaction) {
      const expired = dayjs(this.transaction.payment.expiredAt);
      const diff = now.diff(expired, 'seconds');
      if (diff < 0) {
        this.configCountDown.leftTime = Math.abs(now.diff(expired, 'seconds'));
      } else {
        this.isExpired = true;
        this.configCountDown.leftTime = 0;
      }
    }
    this.intervalId = setInterval(() => {
      const expired = dayjs(this.transaction.payment.expiredAt);
      const expiredAdd5Minutes = expired.add(5, "minute");
      const is5Minutes = expiredAdd5Minutes.diff(now, 'minutes');
      if (is5Minutes > 0) {
        this.isCheckStatusManual = true;
        clearInterval(this.intervalId);
        return;
      } else {
        this.checkStatus(this.transaction.invoice);
      }
    }, 5000);
  }

  ngOnDestroy() {
    clearInterval(this.intervalId);
  }

  toPayment(url: string) {
    window.open(url, '_blank');
  }

  checkStatus(transactionRefId: string, typeCheck?: string) {
    const data: DetailPaymentInput = {
      detailPaymentInput: {
        transactionRefId
      }
    };
    this.bosPaymentService.detailPayment(data).subscribe({
      next: ({data}) => {
        if (data.detailPayment.paymentStatus === PaymentStatusTypes.SUCCESS) {
          this.router.navigate(['/payment/paid'], {
            queryParams: {
              total: this.transaction.total,
              type: this.type,
              invoice: this.transaction.invoice
            },
            replaceUrl: true
          });
        }
        if (typeCheck === 'manual' && data.detailPayment.paymentStatus === PaymentStatusTypes.AWAITING_PAYMENT) {
          this.loadingCheckStatus = false;
          alert('Belum terbayar');
        }
      },
      error: () => {
        if (typeCheck === 'manual') {
          this.loadingCheckStatus = false;
          alert('Belum terbayar');
        }
      }
    });
  }

  actionCheckStatus() {
    this.loadingCheckStatus = true;
    this.checkStatus(this.transaction.invoice, 'manual');
  }
}
